import React from "react";
import "./App.css";
import Message from "./components/message";
import Switch from "./components/switch";

//{"calls":[{"url":"/user/page/getMessagesByLogin","method":"POST","params":{"offset":0,"limit":100,"login":"brudna-malpa"}}],"token":"0","lang":"pl"}

interface IState {
  isChecked: boolean;
  listOfMsgs: object[];
  login: string;
  limit: number;
  showButton: boolean;
  listLength: number;
  isActiveList: boolean;
}

class App extends React.PureComponent<{}, IState> {
  constructor(props: any) {
    super(props);

    this.state = {
      isChecked: true,
      limit: 0,
      listLength: 10,
      listOfMsgs: [],
      login: "",
      showButton: false,
      isActiveList: true,
    };

    this.toggleChange = this.toggleChange.bind(this);
    this.fetchMessages = this.fetchMessages.bind(this);
    this.scrollEvent = this.scrollEvent.bind(this);
    this.scrollToTop = this.scrollToTop.bind(this);
    this.switchToTips = this.switchToTips.bind(this);
    this.switchToList = this.switchToList.bind(this);
  }

  public componentDidMount() {
    if (localStorage.getItem("dark_theme")) {
      this.setState({
        isChecked: JSON.parse(localStorage.getItem("dark_theme") || ""),
      });
    }

    document.title = "Lista donate: brudna-malpa";
    this.fetchMessages();

    window.addEventListener("scroll", this.scrollEvent, true);
  }

  public componentWillUnmount() {
    window.removeEventListener("scroll", this.scrollEvent, true);
  }

  public scrollEvent() {

    if ((window.innerHeight + window.pageYOffset) >= (document.body.offsetHeight - window.innerHeight)) {
      this.setState({
        listLength: this.state.listLength + 10,
      });
    }

    if (window.scrollY > window.innerHeight) {
      this.setState({
        showButton: true,
      });
    } else {
      this.setState({
        showButton: false,
      });
    }
  }

  public fetchMessages() {
    const myBody = {
      calls: [{
        method: "POST",
        params: {
          limit: 1,
          login: "brudna-malpa",
          offset: 0,
        },
        url: "/user/page/getMessagesByLogin",
      }],
      lang: "pl",
      token: "0",
    };

    try {
      fetch("https://prod.tipanddonation.com/api/core/batchcall", {
        body: JSON.stringify(myBody),
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
        method: "POST",
      })
      .then((response) => response.json())
      .then((data) => {
				// console.log(data[myBody.calls[0].url].data.results);
				const results = data[myBody.calls[0].url].data.results;

				const createdDonateDate =
					data[myBody.calls[0].url].data.data[0].created_at;
				const dateDonate = new Date(`${createdDonateDate}`);

				const isDateNotOK =
						dateDonate.getFullYear() !== new Date().getFullYear();

				// async function fetchOldMessages() {
				// 	const myBodyOldTemp = {
				// 		calls: [
				// 			{
				// 				method: "POST",
				// 				params: {
				// 					limit: 150,
				// 					login: "brudna-malpa",
				// 					offset: 0
				// 				},
				// 				url: "/user/page/getMessagesByLogin"
				// 			}
				// 		],
				// 		lang: "pl",
				// 		token: "0"
				// 	};

				// 	const newResponse = await fetch(
				// 		"https://prod.tipanddonation.com/core/batchcall",
				// 		{
				// 			body: JSON.stringify(myBodyOldTemp),
				// 			headers: {
				// 				"Access-Control-Allow-Origin": "*",
				// 				"Content-Type": "application/json"
				// 			},
				// 			method: "POST"
				// 		}
				// 	);

				// 	const newData = await newResponse.json();

				// 	return newData[myBodyOldTemp.calls[0].url].data.data;
				// }

				async function fetchNewMessages() {
					myBody.calls[0].params.limit = 150;
					myBody.calls[0].params.offset = (isDateNotOK) ? results - 150 : 0;
					// const myBodyNextTemp = {
					// 	calls: [
					// 		{
					// 			method: "POST",
					// 			params: {
					// 				limit: 150,
					// 				login: "brudna-malpa",
					// 				offset: (isDateNotOK) ?  
					// 					results - 150 :
					// 					0
					// 			},
					// 			url: "/user/page/getMessagesByLogin"
					// 		}
					// 	],
					// 	lang: "pl",
					// 	token: "0"
					// };

					const newResponse = await fetch("https://prod.tipanddonation.com/api/core/batchcall", {
						body: JSON.stringify(myBody),
						headers: {
							"Access-Control-Allow-Origin": "*",
							"Content-Type": "application/json"
						},
						method: "POST"
					});

					const newData = await newResponse.json();


					return newData[myBody.calls[0].url].data.data
				}

				// const createdDonateDate = data[myBody.calls[0].url].data.data[0].created_at;
				// const dateDonate = new Date(`${createdDonateDate}`);

				// if(dateDonate.getFullYear() !== new Date().getFullYear()) {
				// 	fetchNewMessages().then(datas => {
				// 		this.setState({
				// 			listOfMsgs: datas.reverse()
				// 		});
				// 	});
				// } else {
				// 	fetchOldMessages().then(oldWayDatas => {
				// 		this.setState({
				// 			listOfMsgs: oldWayDatas
				// 		});
				// 	})
				// }

				fetchNewMessages().then(datas => {
					this.setState({
						listOfMsgs: (isDateNotOK) ? (datas) ? datas.reverse() : [] : datas 
					})
				})
      });
    } catch (e) {
      // Error
    }
  }

  public renderMsgs() {
    const list = this.state.listOfMsgs;

    const listLimit = list !== undefined ? list.slice(0, this.state.listLength) : undefined;

    return listLimit !== undefined ? listLimit.map((value: any, index) => (
      <Message key={index} value={value} theme={this.state.isChecked} index={index}/>
    )) : <div style={{height: "100vh", fontSize: "4em"}} />;
  }

  public toggleChange() {
    this.setState({
      isChecked: !this.state.isChecked,
    }, () => {
      localStorage.setItem("dark_theme", this.state.isChecked.toString());
    });
  }

  public scrollToTop() {
    window.scroll({top: 0, behavior: "smooth"});
  }

  public switchToTips() {
    this.setState({
      isActiveList: false
    });
  }

  public switchToList() {
		this.fetchMessages();
    this.setState({
      isActiveList: true
		});
  }

  public render() {
    const { isChecked, showButton, isActiveList } = this.state;

    return (
      <div className={`App ${isChecked ? "dark_theme" : "light_theme"}`}>
        <Switch checked={isChecked} toggleChange={this.toggleChange}/>
        {/* <div className={`page-container ${isChecked ? "switchPageButtonsContainerDark" : "switchPageButtonsContainerLight"}`}>
          <button className={`switchPageButton ${isChecked ? isActiveList ? "isActiveButtonDark" : "" : isActiveList ? "isActiveButtonLight" : ""}`} onClick={this.switchToList} disabled={isActiveList ? true : false}>Lista</button>
          <button className={`switchPageButton ${isChecked ? isActiveList ? "" : "isActiveButtonDark" : isActiveList ? "" : "isActiveButtonLight"}`} onClick={this.switchToTips}>Napiwki</button>
        </div> */}
        { isActiveList ? ( 
        <div className="page-container">
          {this.renderMsgs()}
        </div>
        ) : (
          <div className="page-container">
            <iframe title="tipanddonation" style={{width: "100%", height: "94vh"}} src="https://tipanddonation.com/brudna-malpa"></iframe>
          </div>
        )
        }
        <div
             onClick={this.scrollToTop}
             className={`arrow-up ${isChecked ? "arrow-up-dark-theme" : ""}`}
             style={{display: showButton ? "block" : "none"}}
        >
          &#x2191;
        </div>
      </div>
    );
  }
}

export default App;
